
import Vue from "vue";
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);

export default {
components: {
Cell,
XInput,
Group,
XButton,
Radio,
XTextarea,
PopupPicker,
Popup
},
data() {
return {
name: "",
age: "",
sex: "",
idCard: "",
phone: "",
verifyCode: "",
video: "video",
videoFileName: "video",
remark: "",
selectedImage: false,
selectedVideo: false,
sexLabel: "请选择性别",
sexShow: false,
radioOption: [{
key: "1",
value: "男"
},
{
key: "2",
value: "女"
}
]
};
},
methods: {
showRadio() {
this.sexShow = true;
},
radioChange(value, label) {
this.sexLabel = label;
this.sex = value;
this.sexShow = false;
},
onFile(e, displayName) {
let checkResult = this.checkFileSize(e.target.files, displayName); 
if (checkResult) {
if (displayName == "video") {
this.selectedVideo = true;
} else {
var reads = new FileReader();
reads.readAsDataURL(e.target.files[0]);
reads.onload = event => {
this.$refs[displayName].src = event.target.result;
};
this.selectedImage = true;
}
this.$vux.toast.text("上传成功");
}
},
checkFileSize(files, type) {
for (let i = 0; i < files.length; i++) {
if (type == "video" && files[i].size > 1024 * 1024 * 10) {
this.$vux.toast.text("视频大小不能超过10M");
return false;
} else {
if (files[i].size > 1024 * 1024 * 2) {
this.$vux.toast.text("图片大小不能超过2M");
return false;
}
}
}
return true;
},
submitForm() {
if (!this.checkForm()) return;
var form = new FormData(this.$refs.form);
this.$vux.loading.show({
text: "提交中"
});
this.$ajax({
method: "post",
url: "/syzxEnterInfo/add",
data: form,
headers: {
"Content-Type": "multipart/form-data"
}
}).then(result => {
this.$vux.loading.hide();
if (result.data.success && result.data.id) {
localStorage.setItem("userId", result.data.id);
this.$router.push({
name: "activityEnterSuccess"
});
} else {
if (result.data.msg) {
this.$vux.toast.text(result.data.msg);
}
}
});
return;
},
checkForm() {
let namePattern = /^[\u4E00-\u9FA5]{1,5}$/; 
let agePattern = /^\d{1,2}$/; 
let idPattern = /^\d{15}|\d{}18$ /; 
let phonePattern = /^1\d{10}$/; 
if (!this.$refs.name.valid) {
this.$vux.toast.text("请正确的输入参赛者姓名");
return false;
} else if (!this.$refs.age.valid || !agePattern.test(this.age)) {
this.$vux.toast.text("请填写正确的年龄");
return false;
} else if (this.sex == "") {
this.$vux.toast.text("请选择性别");
return false;
} else if (!idPattern.test(this.idCard)) {
this.$vux.toast.text("请填写正确的身份证号");
return false;
} else if (!this.selectedImage) {
this.$vux.toast.text("请上传图片");
return false;
} else if (!this.selectedVideo) {
this.$vux.toast.text("请上传视频");
return false;
} else if (this.remark == "") {
this.$vux.toast.text("请填写介绍信息");
return false;
} else if (!this.$refs.phone.valid) {
this.$vux.toast.text("请填写正确的手机号");
return false;
} else {
return true;
}
}
}
};




